body::after {
    content: "";
    position: fixed;
    inset: 0;
    z-index: -1;
    opacity: 0.1;
    background-image: url("./images/bg.webp");
    background-size: cover;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}